// start
body {
    line-height: $line-height-base;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 400;
    color: $primary;
    // <nav>()<header><header>74px<nav><nav>74px
    padding-top: 72px;
    // aosX
    overflow-x: hidden;
}

a {
    display: block;
}   

ul {
    list-style: none;
}
// bootstrap
p,ul {
    margin: 0;
    padding: 0;
}

h2,h3 {
    margin-bottom: 0;
}

.btn {
    padding: 0;
}
// end
