// 
.second-menu {
    .fixed-top {
        top: 72px;
        z-index: 3;
    }

    .swiper-second-menu {
        .swiper-slide {
            text-align: center;
            font-size: 14px;

            /* Center slide text vertically */
            display: flex;
            justify-content: center;
            align-items: center;

            a:hover {
                background-color: $secondary;
            }
        }

        // swiper
        .swiper-button-next:after,
        .swiper-button-prev:after {
            //  content  ""
            content: '';
        }
    }
}

.second-menu-main {
    // (37+12+12=61)<main>60px=61+60=121
    padding-top: 121px;

    // 
    .nav-item+.nav-item {
        border-top-width: 0 !important;
    }

    article {
        .card-title {
            @include star-rate;
        }
    }
}